/**
 * ËÆæÁΩÆ Store
 * ÁÆ°ÁêÜÊèí‰ª∂ËÆæÁΩÆÔºåÊîØÊåÅ localStorage ÊåÅ‰πÖÂåñ
 */

import { defineStore } from 'pinia';
import type { PluginSettings } from '../types';
import { DEFAULT_SETTINGS, STORAGE_KEYS } from '../types';

interface SettingsState extends PluginSettings {
  /** ÊòØÂê¶Â∑≤ÂàùÂßãÂåñ */
  initialized: boolean;
}

export const useSettingsStore = defineStore('settings', {
  // ==================== State ====================
  state: (): SettingsState => ({
    autoRefreshEnabled: DEFAULT_SETTINGS.autoRefreshEnabled,
    refreshInterval: DEFAULT_SETTINGS.refreshInterval,
    initialized: false,
  }),

  // ==================== Getters ====================
  getters: {
    /**
     * Âà∑Êñ∞Èó¥ÈöîÔºàÁßíÔºâ
     */
    refreshIntervalSeconds(): number {
      return Math.floor(this.refreshInterval / 1000);
    },

    /**
     * Âà∑Êñ∞Èó¥ÈöîÔºàÂàÜÈíüÔºâ
     */
    refreshIntervalMinutes(): number {
      return Math.floor(this.refreshInterval / 60000);
    },

    /**
     * Ê†ºÂºèÂåñÁöÑÂà∑Êñ∞Èó¥Èöî
     */
    formattedRefreshInterval(): string {
      const seconds = this.refreshIntervalSeconds;
      if (seconds < 60) {
        return `${seconds}Áßí`;
      }
      const minutes = this.refreshIntervalMinutes;
      const remainingSeconds = seconds % 60;
      if (remainingSeconds === 0) {
        return `${minutes}ÂàÜÈíü`;
      }
      return `${minutes}ÂàÜ${remainingSeconds}Áßí`;
    },
  },

  // ==================== Actions ====================
  actions: {
    /**
     * ‰ªé localStorage Âä†ËΩΩËÆæÁΩÆ
     */
    loadFromStorage(): void {
      try {
        const stored = localStorage.getItem(STORAGE_KEYS.SETTINGS);
        if (stored) {
          const settings: PluginSettings = JSON.parse(stored);
          this.autoRefreshEnabled = settings.autoRefreshEnabled ?? DEFAULT_SETTINGS.autoRefreshEnabled;
          this.refreshInterval = settings.refreshInterval ?? DEFAULT_SETTINGS.refreshInterval;
          console.log('‚úÖ ËÆæÁΩÆÂ∑≤‰ªé localStorage Âä†ËΩΩ', settings);
        } else {
          console.log('‚ÑπÔ∏è Êú™ÊâæÂà∞‰øùÂ≠òÁöÑËÆæÁΩÆÔºå‰ΩøÁî®ÈªòËÆ§ÂÄº');
        }
      } catch (error) {
        console.error('‚ùå Âä†ËΩΩËÆæÁΩÆÂ§±Ë¥•:', error);
        this.resetToDefaults();
      } finally {
        this.initialized = true;
      }
    },

    /**
     * ‰øùÂ≠òËÆæÁΩÆÂà∞ localStorage
     */
    saveToStorage(): void {
      try {
        const settings: Settings = {
          autoRefreshEnabled: this.autoRefreshEnabled,
          refreshInterval: this.refreshInterval,
        };
        localStorage.setItem(STORAGE_KEYS.SETTINGS, JSON.stringify(settings));
        console.log('üíæ ËÆæÁΩÆÂ∑≤‰øùÂ≠òÂà∞ localStorage', settings);
      } catch (error) {
        console.error('‚ùå ‰øùÂ≠òËÆæÁΩÆÂ§±Ë¥•:', error);
      }
    },

    /**
     * ÂàáÊç¢Ëá™Âä®Âà∑Êñ∞
     */
    toggleAutoRefresh(): void {
      this.autoRefreshEnabled = !this.autoRefreshEnabled;
      this.saveToStorage();
      console.log(`üîÑ Ëá™Âä®Âà∑Êñ∞${this.autoRefreshEnabled ? 'Â∑≤ÂêØÁî®' : 'Â∑≤Á¶ÅÁî®'}`);
    },

    /**
     * ÂêØÁî®Ëá™Âä®Âà∑Êñ∞
     */
    enableAutoRefresh(): void {
      if (!this.autoRefreshEnabled) {
        this.autoRefreshEnabled = true;
        this.saveToStorage();
        console.log('‚úÖ Ëá™Âä®Âà∑Êñ∞Â∑≤ÂêØÁî®');
      }
    },

    /**
     * Á¶ÅÁî®Ëá™Âä®Âà∑Êñ∞
     */
    disableAutoRefresh(): void {
      if (this.autoRefreshEnabled) {
        this.autoRefreshEnabled = false;
        this.saveToStorage();
        console.log('‚è∏Ô∏è Ëá™Âä®Âà∑Êñ∞Â∑≤Á¶ÅÁî®');
      }
    },

    /**
     * ËÆæÁΩÆÂà∑Êñ∞Èó¥Èöî
     * @param interval - Èó¥ÈöîÊó∂Èó¥ÔºàÊØ´ÁßíÔºâ
     */
    setRefreshInterval(interval: number): void {
      // ÈôêÂà∂ÊúÄÂ∞èÈó¥Èöî‰∏∫ 5 Áßí
      const minInterval = 5000;
      this.refreshInterval = Math.max(interval, minInterval);
      this.saveToStorage();
      console.log(`‚è±Ô∏è Âà∑Êñ∞Èó¥ÈöîÂ∑≤ËÆæÁΩÆ‰∏∫ ${this.formattedRefreshInterval}`);
    },

    /**
     * ËÆæÁΩÆÂà∑Êñ∞Èó¥ÈöîÔºàÁßíÔºâ
     * @param seconds - ÁßíÊï∞
     */
    setRefreshIntervalSeconds(seconds: number): void {
      this.setRefreshInterval(seconds * 1000);
    },

    /**
     * ËÆæÁΩÆÂà∑Êñ∞Èó¥ÈöîÔºàÂàÜÈíüÔºâ
     * @param minutes - ÂàÜÈíüÊï∞
     */
    setRefreshIntervalMinutes(minutes: number): void {
      this.setRefreshInterval(minutes * 60000);
    },

    /**
     * ÈáçÁΩÆ‰∏∫ÈªòËÆ§ËÆæÁΩÆ
     */
    resetToDefaults(): void {
      this.autoRefreshEnabled = DEFAULT_SETTINGS.autoRefreshEnabled;
      this.refreshInterval = DEFAULT_SETTINGS.refreshInterval;
      this.saveToStorage();
      console.log('üîÑ ËÆæÁΩÆÂ∑≤ÈáçÁΩÆ‰∏∫ÈªòËÆ§ÂÄº');
    },

    /**
     * ÂàùÂßãÂåñËÆæÁΩÆ
     * ‰ªé localStorage Âä†ËΩΩÔºåÂ¶ÇÊûú‰∏çÂ≠òÂú®Âàô‰ΩøÁî®ÈªòËÆ§ÂÄº
     */
    initialize(): void {
      if (!this.initialized) {
        this.loadFromStorage();
      }
    },

    /**
     * Ê∏ÖÈô§‰øùÂ≠òÁöÑËÆæÁΩÆ
     */
    clearStorage(): void {
      try {
        localStorage.removeItem(STORAGE_KEYS.SETTINGS);
        this.resetToDefaults();
        console.log('üóëÔ∏è Â∑≤Ê∏ÖÈô§‰øùÂ≠òÁöÑËÆæÁΩÆ');
      } catch (error) {
        console.error('‚ùå Ê∏ÖÈô§ËÆæÁΩÆÂ§±Ë¥•:', error);
      }
    },

    /**
     * ÂØºÂá∫ËÆæÁΩÆÔºàÁî®‰∫éÂ§á‰ªΩÔºâ
     */
    exportSettings(): PluginSettings {
      return {
        autoRefreshEnabled: this.autoRefreshEnabled,
        refreshInterval: this.refreshInterval,
      };
    },

    /**
     * ÂØºÂÖ•ËÆæÁΩÆÔºàÁî®‰∫éÊÅ¢Â§çÔºâ
     * @param settings - ËÆæÁΩÆÂØπË±°
     */
    importSettings(settings: Partial<PluginSettings>): void {
      if (settings.autoRefreshEnabled !== undefined) {
        this.autoRefreshEnabled = settings.autoRefreshEnabled;
      }
      if (settings.refreshInterval !== undefined) {
        this.refreshInterval = settings.refreshInterval;
      }
      this.saveToStorage();
      console.log('üì• ËÆæÁΩÆÂ∑≤ÂØºÂÖ•', settings);
    },
  },
});
