/**
 * ÂÖ¨ÂØìÊï∞ÊçÆ Store
 * ÁÆ°ÁêÜ MVU Êï∞ÊçÆ„ÄÅÊ•ºÂ±Ç„ÄÅÊàøÈó¥„ÄÅÁßüÂÆ¢‰ø°ÊÅØ
 */

import { defineStore } from 'pinia';
import type { ApartmentData, Floor, Room, MVUGetDataParams, MVUResult } from '../types';
import { RETRY_CONFIG } from '../types';
import { getMVUInstance, unwrapMVUString } from '../utils/mvuHelper';

interface ApartmentState {
  /** MVU ÂéüÂßãÊï∞ÊçÆ */
  rawData: ApartmentData | null;
  /** ÊòØÂê¶Ê≠£Âú®Âä†ËΩΩ */
  loading: boolean;
  /** ÈîôËØØ‰ø°ÊÅØ */
  error: string | null;
  /** ÂΩìÂâçÈáçËØïÊ¨°Êï∞ */
  retryCount: number;
  /** ÊúÄÂêéÊõ¥Êñ∞Êó∂Èó¥ */
  lastUpdateTime: number | null;
}

export const useApartmentStore = defineStore('apartment', {
  // ==================== State ====================
  state: (): ApartmentState => ({
    rawData: null,
    loading: false,
    error: null,
    retryCount: 0,
    lastUpdateTime: null,
  }),

  // ==================== Getters ====================
  getters: {
    /**
     * ÊòØÂê¶ÊúâÊï∞ÊçÆ
     */
    hasData: (state): boolean => {
      return state.rawData !== null && state.rawData !== undefined;
    },

    /**
     * Ê•ºÂ±ÇÂàóË°®
     */
    floors: (state): Floor[] => {
      if (!state.rawData?.floors) return [];
      return state.rawData.floors;
    },

    /**
     * Ê•ºÂ±ÇÊï∞Èáè
     */
    floorCount(): number {
      return this.floors.length;
    },

    /**
     * ÊâÄÊúâÊàøÈó¥ÂàóË°®ÔºàÊâÅÂπ≥ÂåñÔºâ
     */
    allRooms(): Room[] {
      const rooms: Room[] = [];
      this.floors.forEach(floor => {
        if (floor.rooms && Array.isArray(floor.rooms)) {
          rooms.push(...floor.rooms);
        }
      });
      return rooms;
    },

    /**
     * ÊàøÈó¥ÊÄªÊï∞
     */
    roomCount(): number {
      return this.allRooms.length;
    },

    /**
     * Â∑≤Âá∫ÁßüÊàøÈó¥Êï∞Èáè
     */
    rentedRoomCount(): number {
      return this.allRooms.filter(room => {
        const type = unwrapMVUString(room.type);
        return type === 'Ê†áÂáÜÂ•óÈó¥' && room.tenant;
      }).length;
    },

    /**
     * Á©∫ÊàøÈó¥Êï∞Èáè
     */
    emptyRoomCount(): number {
      return this.allRooms.filter(room => {
        const type = unwrapMVUString(room.type);
        return type === 'Á©∫ÊàøÈó¥';
      }).length;
    },

    /**
     * ÂäüËÉΩÊÄßÊàøÈó¥Êï∞Èáè
     */
    functionalRoomCount(): number {
      return this.allRooms.filter(room => {
        const type = unwrapMVUString(room.type);
        return type === 'ÂäüËÉΩÊÄßÊàøÈó¥';
      }).length;
    },

    /**
     * ÂΩìÂâçÊó∂Èó¥
     */
    currentTime: (state): string => {
      if (!state.rawData?.current_time) return '';
      return unwrapMVUString(state.rawData.current_time);
    },

    /**
     * ÂÖ¨ÂØìÂêçÁß∞
     */
    apartmentName: (state): string => {
      if (!state.rawData?.apartment_name) return 'ÁΩëÁ∫¢Â∞èÂå∫';
      return unwrapMVUString(state.rawData.apartment_name, 'ÁΩëÁ∫¢Â∞èÂå∫');
    },

    /**
     * Ê†πÊçÆÊàøÈó¥Âè∑Êü•ÊâæÊàøÈó¥
     */
    getRoomByNumber: (state) => {
      return (roomNumber: string): Room | undefined => {
        for (const floor of state.rawData?.floors || []) {
          if (!floor.rooms) continue;
          const room = floor.rooms.find(r => unwrapMVUString(r.number) === roomNumber);
          if (room) return room;
        }
        return undefined;
      };
    },

    /**
     * Ê†πÊçÆÊ•ºÂ±ÇÁºñÂè∑Êü•ÊâæÊ•ºÂ±Ç
     */
    getFloorByLevel: (state) => {
      return (level: number): Floor | undefined => {
        return state.rawData?.floors?.find(f => {
          const floorLevel = Array.isArray(f.level) ? f.level[0] : f.level;
          return floorLevel === level;
        });
      };
    },

    /**
     * ÊòØÂê¶ÂèØ‰ª•ÈáçËØï
     */
    canRetry: (state): boolean => {
      return state.retryCount < RETRY_CONFIG.MAX_RETRIES;
    },
  },

  // ==================== Actions ====================
  actions: {
    /**
     * Âä†ËΩΩ MVU Êï∞ÊçÆ
     */
    async loadData(): Promise<boolean> {
      this.loading = true;
      this.error = null;

      try {
        // Ê£ÄÊü• MVU ÊòØÂê¶ÂèØÁî®
        const mvu = getMVUInstance();
        if (!mvu) {
          throw new Error('MVU Ê°ÜÊû∂Êú™Âä†ËΩΩÊàñÊó†Ê≥ïËÆøÈóÆ');
        }

        // Ëé∑ÂèñÊúÄÊñ∞Ê∂àÊÅØÁöÑ MVU Êï∞ÊçÆ
        const params: MVUGetDataParams = {
          type: 'message',
          message_id: 'latest',
        };

        const mvuResult: MVUResult | undefined = mvu.getMvuData(params);
        const data = mvuResult?.stat_data;

        if (!data) {
          throw new Error('MVU Êï∞ÊçÆ‰∏∫Á©∫');
        }

        // ‰øùÂ≠òÊï∞ÊçÆ
        this.rawData = data;
        this.lastUpdateTime = Date.now();
        this.retryCount = 0;
        this.loading = false;

        console.log('‚úÖ ÂÖ¨ÂØìÊï∞ÊçÆÂä†ËΩΩÊàêÂäü', data);
        return true;
      } catch (error) {
        const errorMessage = (error as Error).message;
        this.error = errorMessage;
        this.loading = false;

        console.error('‚ùå Âä†ËΩΩÂÖ¨ÂØìÊï∞ÊçÆÂ§±Ë¥•:', errorMessage);

        // Â¶ÇÊûúÂèØ‰ª•ÈáçËØïÔºåÂàôËá™Âä®ÈáçËØï
        if (this.canRetry) {
          this.retryCount++;
          console.log(`üîÑ Á¨¨ ${this.retryCount}/${RETRY_CONFIG.MAX_RETRIES} Ê¨°ÈáçËØï...`);
          
          await new Promise(resolve => setTimeout(resolve, RETRY_CONFIG.RETRY_DELAY));
          return this.loadData();
        }

        return false;
      }
    },

    /**
     * Âà∑Êñ∞Êï∞ÊçÆ
     */
    async refreshData(): Promise<boolean> {
      console.log('üîÑ Âà∑Êñ∞ÂÖ¨ÂØìÊï∞ÊçÆ...');
      this.retryCount = 0; // ÈáçÁΩÆÈáçËØïËÆ°Êï∞
      return this.loadData();
    },

    /**
     * Ê∏ÖÁ©∫Êï∞ÊçÆ
     */
    clearData(): void {
      this.rawData = null;
      this.error = null;
      this.retryCount = 0;
      this.lastUpdateTime = null;
      console.log('üóëÔ∏è ÂÖ¨ÂØìÊï∞ÊçÆÂ∑≤Ê∏ÖÁ©∫');
    },

    /**
     * ÈáçÁΩÆÈîôËØØÁä∂ÊÄÅ
     */
    resetError(): void {
      this.error = null;
      this.retryCount = 0;
    },

    /**
     * ÊâãÂä®ËÆæÁΩÆÊï∞ÊçÆÔºàÁî®‰∫éÊµãËØïÔºâ
     */
    setData(data: ApartmentData): void {
      this.rawData = data;
      this.lastUpdateTime = Date.now();
      this.error = null;
      this.retryCount = 0;
    },
  },
});

