/**
 * UI Áä∂ÊÄÅ Store
 * ÁÆ°ÁêÜÈù¢ÊùøÊòæÁ§∫„ÄÅÊ®°ÊÄÅÊ°ÜÁä∂ÊÄÅ„ÄÅÂΩìÂâçÈÄâ‰∏≠ÊàøÈó¥Á≠â UI Áõ∏ÂÖ≥Áä∂ÊÄÅ
 */

import { defineStore } from 'pinia';
import type { Room, ModalType } from '../types';

interface UIState {
  /** Èù¢ÊùøÊòØÂê¶ÂèØËßÅ */
  isPanelVisible: boolean;
  /** ÂΩìÂâçÊâìÂºÄÁöÑÊ®°ÊÄÅÊ°ÜÁ±ªÂûã */
  activeModal: ModalType;
  /** ÂΩìÂâçÈÄâ‰∏≠ÁöÑÊàøÈó¥ */
  currentRoom: Room | null;
  /** ÂΩìÂâçÊ≠£Âú®Ë£Ö‰øÆÁöÑÊàøÈó¥Âè∑ */
  currentRenovatingRoom: string;
  /** ÂΩìÂâçÂæÖÊãÜÈô§ÁöÑÊàøÈó¥Âè∑ */
  currentRoomForDemolish: string;
}

export const useUIStore = defineStore('ui', {
  // ==================== State ====================
  state: (): UIState => ({
    isPanelVisible: false,
    activeModal: null,
    currentRoom: null,
    currentRenovatingRoom: '',
    currentRoomForDemolish: '',
  }),

  // ==================== Getters ====================
  getters: {
    /**
     * ÊòØÂê¶ÊúâÊ®°ÊÄÅÊ°ÜÊâìÂºÄ
     */
    hasModalOpen: (state): boolean => {
      return state.activeModal !== null;
    },

    /**
     * ÊòØÂê¶‰∏∫ÊàøÈó¥ËØ¶ÊÉÖÊ®°ÊÄÅÊ°Ü
     */
    isRoomModalOpen: (state): boolean => {
      return state.activeModal === 'room';
    },

    /**
     * ÊòØÂê¶‰∏∫ÊãõÂãüÁßüÂÆ¢Ê®°ÊÄÅÊ°Ü
     */
    isRecruitmentModalOpen: (state): boolean => {
      return state.activeModal === 'recruitment';
    },

    /**
     * ÊòØÂê¶‰∏∫Ë£Ö‰øÆÈÄâÊã©Ê®°ÊÄÅÊ°Ü
     */
    isRenovateModalOpen: (state): boolean => {
      return state.activeModal === 'renovate';
    },

    /**
     * ÊòØÂê¶‰∏∫ÂäüËÉΩÊÄßÊàøÈó¥ËæìÂÖ•Ê®°ÊÄÅÊ°Ü
     */
    isFunctionalInputModalOpen: (state): boolean => {
      return state.activeModal === 'functional-input';
    },

    /**
     * ÂΩìÂâçÊàøÈó¥Âè∑
     */
    currentRoomNumber: (state): string => {
      if (!state.currentRoom) return '';
      const number = state.currentRoom.number;
      return Array.isArray(number) ? number[0] || '' : number || '';
    },
  },

  // ==================== Actions ====================
  actions: {
    /**
     * ÊâìÂºÄÈù¢Êùø
     */
    openPanel(): void {
      this.isPanelVisible = true;
      console.log('üìÇ Èù¢ÊùøÂ∑≤ÊâìÂºÄ');
    },

    /**
     * ÂÖ≥Èó≠Èù¢Êùø
     */
    closePanel(): void {
      this.isPanelVisible = false;
      console.log('üìÅ Èù¢ÊùøÂ∑≤ÂÖ≥Èó≠');
    },

    /**
     * ÂàáÊç¢Èù¢ÊùøÊòæÁ§∫Áä∂ÊÄÅ
     */
    togglePanel(): void {
      this.isPanelVisible = !this.isPanelVisible;
      console.log(`üìÇ Èù¢Êùø${this.isPanelVisible ? 'Â∑≤ÊâìÂºÄ' : 'Â∑≤ÂÖ≥Èó≠'}`);
    },

    /**
     * ÊâìÂºÄÊ®°ÊÄÅÊ°Ü
     * @param type - Ê®°ÊÄÅÊ°ÜÁ±ªÂûã
     * @param room - Áõ∏ÂÖ≥ÊàøÈó¥ÔºàÂèØÈÄâÔºâ
     */
    openModal(type: ModalType, room?: Room): void {
      this.activeModal = type;
      if (room) {
        this.currentRoom = room;
      }
      console.log(`üî≤ ÊâìÂºÄÊ®°ÊÄÅÊ°Ü: ${type}`, room);
    },

    /**
     * ÂÖ≥Èó≠Ê®°ÊÄÅÊ°Ü
     */
    closeModal(): void {
      const previousModal = this.activeModal;
      this.activeModal = null;
      this.currentRoom = null;
      this.currentRenovatingRoom = '';
      this.currentRoomForDemolish = '';
      console.log(`üî≥ ÂÖ≥Èó≠Ê®°ÊÄÅÊ°Ü: ${previousModal}`);
    },

    /**
     * ÊâìÂºÄÊàøÈó¥ËØ¶ÊÉÖÊ®°ÊÄÅÊ°Ü
     * @param room - ÊàøÈó¥Êï∞ÊçÆ
     */
    openRoomModal(room: Room): void {
      this.openModal('room', room);
    },

    /**
     * ÊâìÂºÄÊãõÂãüÁßüÂÆ¢Ê®°ÊÄÅÊ°Ü
     */
    openRecruitmentModal(): void {
      this.openModal('recruitment');
    },

    /**
     * ÊâìÂºÄË£Ö‰øÆÈÄâÊã©Ê®°ÊÄÅÊ°Ü
     * @param roomNumber - ÊàøÈó¥Âè∑
     */
    openRenovateModal(roomNumber: string): void {
      this.currentRenovatingRoom = roomNumber;
      this.openModal('renovate');
    },

    /**
     * ÊâìÂºÄÂäüËÉΩÊÄßÊàøÈó¥ËæìÂÖ•Ê®°ÊÄÅÊ°Ü
     * @param roomNumber - ÊàøÈó¥Âè∑
     */
    openFunctionalInputModal(roomNumber: string): void {
      this.currentRenovatingRoom = roomNumber;
      this.openModal('functional-input');
    },

    /**
     * ËÆæÁΩÆÂΩìÂâçÊàøÈó¥
     * @param room - ÊàøÈó¥Êï∞ÊçÆ
     */
    setCurrentRoom(room: Room | null): void {
      this.currentRoom = room;
    },

    /**
     * ËÆæÁΩÆÂΩìÂâçË£Ö‰øÆÊàøÈó¥Âè∑
     * @param roomNumber - ÊàøÈó¥Âè∑
     */
    setCurrentRenovatingRoom(roomNumber: string): void {
      this.currentRenovatingRoom = roomNumber;
    },

    /**
     * ËÆæÁΩÆÂΩìÂâçÂæÖÊãÜÈô§ÊàøÈó¥Âè∑
     * @param roomNumber - ÊàøÈó¥Âè∑
     */
    setCurrentRoomForDemolish(roomNumber: string): void {
      this.currentRoomForDemolish = roomNumber;
    },

    /**
     * ÈáçÁΩÆÊâÄÊúâ UI Áä∂ÊÄÅ
     */
    reset(): void {
      this.isPanelVisible = false;
      this.activeModal = null;
      this.currentRoom = null;
      this.currentRenovatingRoom = '';
      this.currentRoomForDemolish = '';
      console.log('üîÑ UI Áä∂ÊÄÅÂ∑≤ÈáçÁΩÆ');
    },
  },
});

