`, "",{"version":3,"sources":["webpack://./src/ÁΩëÁ∫¢Â∞èÂå∫vueÁâà/components/ToggleButton.vue"],"names":[],"mappings":"AA+FA,iCACE,yBAA0B,CAC1B,UAAW,CACX,WAAY,CACZ,sCAAuC,CACvC,iBAAkB,CAClB,YAAa,CACb,kBAAmB,CACnB,sBAAuB,CACvB,WAAY,CACZ,sCAAuC,CACvC,+BAAgC,CAChC,gBAAiB,CACjB,wBAAyB,CACzB,iBAAkB,CAClB,cAAe,CACf,sCAA0C,CAC1C,UAAW,CACX,8HAGF,CAEA,uCACE,qBACF,CAEA,0CACE,0BAA2B,CAC3B,WAAY,CACZ,+CAAgD,CAChD,0BACF","sourcesContent":["<template>\n  <div\n    ref=\"buttonRef\"\n    class=\"ngq-toggle-btn\"\n    :class=\"{ dragging: isDragging }\"\n    :style=\"buttonStyle\"\n    :title=\"tooltipText\"\n  >\n    üè¢\n  </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { ref, computed, onMounted } from 'vue';\nimport { useUIStore } from '../stores/ui';\nimport { useDrag } from '../composables/useDrag';\nimport { usePosition } from '../composables/usePosition';\n\n// Stores\nconst uiStore = useUIStore();\n\n// ÂÖÉÁ¥†ÂºïÁî®\nconst buttonRef = ref<HTMLElement | null>(null);\n\n// ‰ΩçÁΩÆÁÆ°ÁêÜ\nconst { position, savePosition, restorePosition } = usePosition({\n  key: 'button',\n  defaultPosition: { left: 20, top: 100 },\n  autoSave: false, // ÊâãÂä®‰øùÂ≠òÔºåÂú®ÊãñÂä®ÁªìÊùüÊó∂\n});\n\n// ÊãñÂä®ÂäüËÉΩ\nconst { isDragging } = useDrag({\n  elementRef: buttonRef,\n  enabled: true,\n  constrainToViewport: true,\n  boundaryOffset: 10,\n  clickThreshold: 5,\n  onDragStart: (pos) => {\n    console.log('üñ±Ô∏è ÂºÄÂßãÊãñÂä®ÊåâÈíÆ', pos);\n  },\n  onDragMove: (pos) => {\n    // ÂÆûÊó∂Êõ¥Êñ∞‰ΩçÁΩÆÔºà‰ΩÜ‰∏ç‰øùÂ≠òÔºâ\n    position.value = pos;\n  },\n  onDragEnd: (pos, isClick) => {\n    console.log('‚úÖ ÊãñÂä®ÁªìÊùü', { position: pos, isClick });\n    \n    // ‰øùÂ≠ò‰ΩçÁΩÆ\n    savePosition(pos);\n    \n    // Â¶ÇÊûúÊòØÁÇπÂáªÔºåÂàáÊç¢Èù¢Êùø\n    if (isClick) {\n      handleClick();\n    }\n  },\n});\n\n/**\n * ÊåâÈíÆÊ†∑Âºè\n */\nconst buttonStyle = computed(() => ({\n  left: `${position.value.left}px`,\n  top: `${position.value.top}px`,\n}));\n\n/**\n * ÊèêÁ§∫ÊñáÊú¨\n */\nconst tooltipText = computed(() => {\n  return uiStore.isPanelVisible ? 'ÁÇπÂáªÂÖ≥Èó≠Èù¢Êùø' : 'ÁÇπÂáªÊâìÂºÄÈù¢Êùø';\n});\n\n/**\n * Â§ÑÁêÜÁÇπÂáª\n */\nconst handleClick = () => {\n  console.log('üé® Ê£ÄÊµãÂà∞ÁÇπÂáªÔºåÂàáÊç¢Èù¢Êùø');\n  uiStore.togglePanel();\n  \n  // Â¶ÇÊûúÊâìÂºÄÈù¢ÊùøÔºåÂèØ‰ª•Ëß¶ÂèëÊï∞ÊçÆÂä†ËΩΩ\n  // ËøôÈáåÊöÇÊó∂‰∏çÂä†ËΩΩÔºåÁ≠â‰∏ªÈù¢ÊùøÁªÑ‰ª∂ÂàõÂª∫ÂêéÂÜçÂ§ÑÁêÜ\n};\n\n/**\n * ÂàùÂßãÂåñ\n */\nonMounted(() => {\n  // ÊÅ¢Â§ç‰øùÂ≠òÁöÑ‰ΩçÁΩÆ\n  const savedPos = restorePosition();\n  console.log('‚úÖ ToggleButton Â∑≤ÊåÇËΩΩÔºå‰ΩçÁΩÆ:', savedPos);\n});\n</script>\n\n<style scoped>\n.ngq-toggle-btn {\n  position: fixed !important;\n  width: 64px;\n  height: 64px;\n  background: var(--apt-gradient-primary);\n  border-radius: 50%;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  cursor: grab;\n  z-index: var(--apt-z-button) !important;\n  box-shadow: var(--apt-shadow-md);\n  user-select: none;\n  -webkit-user-select: none;\n  touch-action: none;\n  font-size: 28px;\n  border: 3px solid rgba(255, 255, 255, 0.3);\n  color: #fff;\n  transition: transform var(--apt-transition-normal), \n              box-shadow var(--apt-transition-normal), \n              opacity var(--apt-transition-normal);\n}\n\n.ngq-toggle-btn:hover {\n  transform: scale(1.05);\n}\n\n.ngq-toggle-btn.dragging {\n  cursor: grabbing !important;\n  opacity: 0.9;\n  z-index: var(--apt-z-button-dragging) !important;\n  transition: none !important;\n}\n</style>\n\n"],"sourceRoot":""}]);
