/**
 * ÁΩëÁ∫¢Â∞èÂå∫ Vue Áâà - ÂÖ•Âè£Êñá‰ª∂
 * 
 * ÂàùÂßãÂåñ Vue Â∫îÁî®Âπ∂ÊåÇËΩΩÂà∞ SillyTavern È°µÈù¢
 */

import { createApp } from 'vue';
import { createPinia } from 'pinia';
import App from './App.vue';

// ÂØºÂÖ•ÂÖ®Â±ÄÊ†∑Âºè
import './styles/common.css';

console.log('üè¢ ÁΩëÁ∫¢Â∞èÂå∫ Vue ÁâàÊ≠£Âú®ÂàùÂßãÂåñ...');

/**
 * ÂàõÂª∫ Vue Â∫îÁî®
 */
function createNgqApp() {
  const app = createApp(App);
  const pinia = createPinia();

  app.use(pinia);

  return app;
}

/**
 * Ëé∑ÂèñÁõÆÊ†áÊñáÊ°£ÔºàSillyTavern ÁöÑÈ°∂Â±ÇÊñáÊ°£Ôºâ
 */
function getTargetDocument(): Document {
  return window.top?.document || document;
}

/**
 * ÂàõÂª∫ÊåÇËΩΩÂÆπÂô®
 */
function createMountContainer(targetDoc: Document): HTMLDivElement {
  const container = targetDoc.createElement('div');
  container.id = 'ngq-vue-app';
  container.setAttribute('data-script-id', getScriptId());
  return container;
}

/**
 * ÈîÄÊØÅÊåÇËΩΩÂÆπÂô®
 */
function destroyMountContainer(targetDoc: Document): void {
  const container = targetDoc.getElementById('ngq-vue-app');
  if (container) {
    container.remove();
    console.log('üóëÔ∏è ÊåÇËΩΩÂÆπÂô®Â∑≤ÁßªÈô§');
  }
}

/**
 * Ëé∑ÂèñËÑöÊú¨ IDÔºàÁî®‰∫éÊ†áËØÜÂíåÊ∏ÖÁêÜÔºâ
 */
function getScriptId(): string {
  // ‰ªéÂΩìÂâçËÑöÊú¨Ë∑ØÂæÑÊèêÂèñ ID
  const scriptPath = import.meta.url;
  const match = scriptPath.match(/ÁΩëÁ∫¢Â∞èÂå∫vueÁâà/);
  return match ? 'ÁΩëÁ∫¢Â∞èÂå∫vueÁâà' : 'ngq-vue';
}

/**
 * ÂàùÂßãÂåñÂ∫îÁî®
 */
function initialize(): void {
  try {
    const targetDoc = getTargetDocument();
    
    // Ê£ÄÊü•ÊòØÂê¶Â∑≤ÁªèÂàùÂßãÂåñ
    if (targetDoc.getElementById('ngq-vue-app')) {
      console.warn('‚ö†Ô∏è Â∫îÁî®Â∑≤ÁªèÂàùÂßãÂåñÔºåË∑≥ËøáÈáçÂ§çÂàùÂßãÂåñ');
      return;
    }

    // ÂàõÂª∫ Vue Â∫îÁî®
    const app = createNgqApp();

    // ÂàõÂª∫ÊåÇËΩΩÂÆπÂô®
    const container = createMountContainer(targetDoc);
    targetDoc.body.appendChild(container);

    // ÊåÇËΩΩÂ∫îÁî®
    app.mount(container);

    console.log('‚úÖ ÁΩëÁ∫¢Â∞èÂå∫ Vue ÁâàÂàùÂßãÂåñÊàêÂäü');

    // È°µÈù¢Âç∏ËΩΩÊó∂Ê∏ÖÁêÜ
    $(window).on('pagehide', () => {
      cleanup(app, targetDoc);
    });
  } catch (error) {
    console.error('‚ùå ÁΩëÁ∫¢Â∞èÂå∫ Vue ÁâàÂàùÂßãÂåñÂ§±Ë¥•:', error);
    toastr?.error('ÁΩëÁ∫¢Â∞èÂå∫Êèí‰ª∂ÂàùÂßãÂåñÂ§±Ë¥•');
  }
}

/**
 * Ê∏ÖÁêÜÂ∫îÁî®
 */
function cleanup(app: any, targetDoc: Document): void {
  try {
    app.unmount();
    destroyMountContainer(targetDoc);
    console.log('üßπ ÁΩëÁ∫¢Â∞èÂå∫ Vue ÁâàÂ∑≤Ê∏ÖÁêÜ');
  } catch (error) {
    console.error('‚ùå Ê∏ÖÁêÜÂ§±Ë¥•:', error);
  }
}

// Á≠âÂæÖ DOM Âä†ËΩΩÂÆåÊàêÂêéÂàùÂßãÂåñ
$(() => {
  initialize();
});

// ÂÖ®Â±ÄÁ±ªÂûãÂ£∞Êòé
declare global {
  const $: JQueryStatic;
  const toastr: {
    success: (message: string, title?: string) => void;
    error: (message: string, title?: string) => void;
    warning: (message: string, title?: string) => void;
    info: (message: string, title?: string) => void;
  } | undefined;
}

